// App internal variables
@main-header-height: 82px;
@main-nav-width: 86px;
@main-nav-width-collapse: 225px;

body.ss-dashboard-wrapper {
	background-color: #f7f9ff;
	div.dash-header,
	div.dash-nav,
	div.dash-footer {
		display: none !important;
	}

	.dashboard {
		height: auto;
		.pagecontent {
			background-color: #f7f9ff;
			height: auto;
			min-height: inherit;
			overflow-x: hidden;
		}
	}

	.section {
		min-width: auto;
	}

	> div:not([id="SS-DashboardWrapper"]),
	> div:not(.UIPromtBoxBG) {
		position: relative;
		z-index: 1;
	}

	&.newui {
		.UIPromtBox,
		.UIPromtBox.master {
			z-index: 99 !important;
		}
		.UIPromtBoxBG {
			top: @main-header-height;
			left: @main-nav-width;
			z-index: 99 !important;
		}

		&.menu-show-full .UIPromtBoxBG {
			left: @main-nav-width-collapse;
		}
	}

	@media screen and (min-width: 1601px) {
		div.pagecontent {
			padding: @main-header-height 0 0 @main-nav-width-collapse !important;
		}
	}

	@media screen and (min-width: 992px) and (max-width: 1600px) {
		div.pagecontent {
			padding: @main-header-height 0 0 @main-nav-width !important;
			background-color: #f7f9ff;
		}

		&.menu-show-full div.pagecontent {
			padding-left: @main-nav-width-collapse !important;
		}
	}

	@media screen and (min-width: 768px) {
		div.pagecontent {
			padding-top: @main-header-height !important;
		}
	}

	@media screen and (min-width: 992px) {
		.contentwrapper {
			margin: 0 !important;
			padding: 20px !important;
		}
	}

	.contentwrapper {
		margin: 0 !important;

		@media screen and (max-width: 767px) {
			padding-top: 56px;
		}
	}

	.toaster {
		top: @main-header-height;
	}

	.uimodalbox,
	#uimodalbg,
	.uimodalbg,
	.uimodalbox .uimodalcontainer {
		top: @main-header-height;
		left: @main-nav-width;
		height: ~"calc(100% - @{main-header-height})";
		width: ~"calc(100% - @{main-nav-width})";

		@media screen and (min-width: 1601px) {
			width: ~"calc(100% - @{main-nav-width-collapse})";
			left: @main-nav-width-collapse !important;
		}

		@media screen and (max-width: 991px) {
			width: 100%;
			left: 0;
		}

		@media screen and (max-width: 767px) {
			top: 52px;
			height: calc(100% - 52px);
		}
	}

	@media screen and (min-width: 992px) and (max-width: 1600px) {
		&.menu-show-full {
			#uimodalbg,
			.uimodalbg,
			.uimodalbox,
			.uimodalbox .uimodalcontainer {
				left: @main-nav-width-collapse;
				width: ~"calc(100% - @{main-nav-width-collapse})";
			}
		}
	}
}
